// Customizable dashboards with tabbed interface
// Supports: Default, Public, Role-Restricted, User-Specific dashboards
// ==========================================

// Widget Types
type WidgetType = 'stat' | 'chart' | 'list' | 'table'
type ChartType = 'line' | 'bar' | 'doughnut' | 'pie'

interface Widget {
  id: string
  type: WidgetType
  title: string
  position: number
  visible: boolean
  size: 'small' | 'medium' | 'large' | 'full'
  chartType?: ChartType
  data?: any
}

// Dashboard Visibility Types
type DashboardVisibility = 'public' | 'role' | 'user'

interface DashboardAccess {
  visibility: DashboardVisibility
  // For role-restricted dashboards
  allowedRoles?: string[]
  // For user-specific dashboards
  allowedUserIds?: string[]
}

interface Dashboard {
  id: string
  name: string
  description?: string
  icon?: string
  isDefault: boolean // Is this the user's default dashboard
  isSystemDefault: boolean // Is this the system-wide default
  createdBy: string
  createdAt: string
  updatedAt: string
  access: DashboardAccess
  widgets: Widget[]
}

