export default function SubAccountDashboard() {
  const pathname = usePathname()
  
  // Dashboard state
  const [dashboards, setDashboards] = useState<Dashboard[]>(MOCK_DASHBOARDS)
  const [selectedDashboardId, setSelectedDashboardId] = useState<string>('dash-001')
  const [userDefaultDashboardId, setUserDefaultDashboardId] = useState<string>('dash-001')
  
  // UI state
  const [showDatePicker, setShowDatePicker] = useState(false)
  const [showCustomCalendar, setShowCustomCalendar] = useState(false)
  const [showWidgetManager, setShowWidgetManager] = useState(false)
  const [showAddWidget, setShowAddWidget] = useState(false)
  const [showCreateDashboard, setShowCreateDashboard] = useState(false)
  const [showDashboardSettings, setShowDashboardSettings] = useState(false)
  const [dateRange, setDateRange] = useState('Last 30 Days')
  const [customStartDate, setCustomStartDate] = useState('')
  const [customEndDate, setCustomEndDate] = useState('')
  const [editMode, setEditMode] = useState(false)
  const [draggedWidget, setDraggedWidget] = useState<string | null>(null)
  
  // Create/Edit Dashboard form state
  const [dashboardForm, setDashboardForm] = useState({
    name: '',
    description: '',
    icon: 'üìä',
    visibility: 'public' as DashboardVisibility,
    allowedRoles: [] as string[],
    allowedUserIds: [] as string[],
  })
  const [editingDashboard, setEditingDashboard] = useState<Dashboard | null>(null)

  // Get current dashboard
  const currentDashboard = dashboards.find(d => d.id === selectedDashboardId) || dashboards[0]
  
  // Get dashboards visible to current user
  const visibleDashboards = dashboards.filter(dashboard => {
    if (dashboard.access.visibility === 'public') return true
    if (dashboard.access.visibility === 'role' && dashboard.access.allowedRoles) {
      return dashboard.access.allowedRoles.includes(CURRENT_USER.role)
    }
    if (dashboard.access.visibility === 'user' && dashboard.access.allowedUserIds) {
      return dashboard.access.allowedUserIds.includes(CURRENT_USER.id)
    }
    // Also show dashboards created by current user
    if (dashboard.createdBy === CURRENT_USER.id) return true
    return false
  })
  
  // Get user's default dashboard
  const defaultDashboard = visibleDashboards.find(d => d.id === userDefaultDashboardId) || visibleDashboards[0]
  
  // Widgets for current dashboard
  const [widgets, setWidgets] = useState<Widget[]>(currentDashboard?.widgets || DEFAULT_WIDGETS)

  // Update widgets when dashboard changes
  useEffect(() => {
    if (currentDashboard) {
      setWidgets(currentDashboard.widgets)
    }
  }, [selectedDashboardId])

  // Mock date ranges
  const dateRanges = [
    'Today',
    'Yesterday',
    'Last 7 Days',
    'Last 30 Days',
    'Last 90 Days',
    'This Month',
    'Last Month',
    'This Year',
    'Custom Range',
  ]

  // Available widgets to add
  const availableWidgets = [
    { id: 'new-stat-appointments', type: 'stat', title: 'Scheduled Appointments', icon: 'üìÖ' },
    { id: 'new-stat-conversion', type: 'stat', title: 'Conversion Rate', icon: 'üìä' },
    { id: 'new-stat-response', type: 'stat', title: 'Avg Response Time', icon: '‚è±Ô∏è' },
    { id: 'new-chart-pie', type: 'chart', title: 'Traffic Sources', chartType: 'pie', icon: 'ü•ß' },
    { id: 'new-chart-line', type: 'chart', title: 'Campaign Performance', chartType: 'line', icon: 'üìà' },
    { id: 'new-list-tasks', type: 'list', title: 'Pending Tasks', icon: '‚úÖ' },
  ]

  // Dashboard icon options
  const dashboardIcons = ['üìä', 'üí∞', 'üì£', 'üéß', 'üë§', 'üéØ', 'üìà', 'üèÜ', '‚≠ê', 'üî•', 'üí°', 'üöÄ']

  // Chart.js rendering (client-side only)
  useEffect(() => {
    if (typeof window !== 'undefined') {
      import('chart.js/auto').then((ChartJS) => {
        const Chart = ChartJS.default

        widgets.filter(w => w.type === 'chart' && w.visible).forEach(widget => {
          const canvas = document.getElementById(`chart-${widget.id}`) as HTMLCanvasElement
          if (canvas) {
            const ctx = canvas.getContext('2d')
            if (ctx) {
              const existingChart = Chart.getChart(canvas)
              if (existingChart) {
                existingChart.destroy()
              }

              new Chart(ctx, {
                type: widget.chartType || 'line',
                data: widget.data,
                options: {
                  responsive: true,
                  maintainAspectRatio: false,
                  plugins: {
                    legend: {
                      display: widget.chartType !== 'line',
                      position: 'bottom'
                    }
                  }
                }
              })
            }
          }
        })
      })
    }
  }, [widgets, editMode])

  // Handlers
  const handleSelectDashboard = (dashboardId: string) => {
    setSelectedDashboardId(dashboardId)
    setEditMode(false)
    setShowWidgetManager(false)
  }

  const handleSetAsDefault = (dashboardId: string) => {
    setUserDefaultDashboardId(dashboardId)
    setDashboards(prev => prev.map(d => ({
      ...d,
      isDefault: d.id === dashboardId
    })))
  }

  const handleEditDashboard = () => {
    setEditMode(!editMode)
    if (!editMode) {
      setShowWidgetManager(true)
    } else {
      setShowWidgetManager(false)
    }
  }

  const handleOpenDashboardSettings = () => {
    if (currentDashboard) {
      setEditingDashboard(currentDashboard)
      setDashboardForm({
        name: currentDashboard.name,
        description: currentDashboard.description || '',
        icon: currentDashboard.icon || 'üìä',
        visibility: currentDashboard.access.visibility,
        allowedRoles: currentDashboard.access.allowedRoles || [],
        allowedUserIds: currentDashboard.access.allowedUserIds || [],
      })
      setShowDashboardSettings(true)
    }
  }

  const handleCreateDashboard = () => {
    setEditingDashboard(null)
    setDashboardForm({
      name: '',
      description: '',
      icon: 'üìä',
      visibility: 'public',
      allowedRoles: [],
      allowedUserIds: [],
    })
    setShowCreateDashboard(true)
  }

  const handleSaveDashboard = () => {
    if (!dashboardForm.name.trim()) return

    if (editingDashboard) {
      // Update existing dashboard
      setDashboards(prev => prev.map(d => 
        d.id === editingDashboard.id
          ? {
              ...d,
              name: dashboardForm.name,
              description: dashboardForm.description,
              icon: dashboardForm.icon,
              access: {
                visibility: dashboardForm.visibility,
                allowedRoles: dashboardForm.visibility === 'role' ? dashboardForm.allowedRoles : undefined,
                allowedUserIds: dashboardForm.visibility === 'user' ? dashboardForm.allowedUserIds : undefined,
              },
              updatedAt: new Date().toISOString(),
            }
          : d
      ))
      setShowDashboardSettings(false)
    } else {
      // Create new dashboard
      const newDashboard: Dashboard = {
        id: `dash-${Date.now()}`,
        name: dashboardForm.name,
        description: dashboardForm.description,
        icon: dashboardForm.icon,
        isDefault: false,
        isSystemDefault: false,
        createdBy: CURRENT_USER.id,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
        access: {
          visibility: dashboardForm.visibility,
          allowedRoles: dashboardForm.visibility === 'role' ? dashboardForm.allowedRoles : undefined,
          allowedUserIds: dashboardForm.visibility === 'user' ? dashboardForm.allowedUserIds : undefined,
        },
        widgets: [
          { id: `new-1-${Date.now()}`, type: 'stat', title: 'New Widget', position: 1, visible: true, size: 'small', data: { value: '0', change: '0%', icon: 'üìä', changeType: 'neutral' } },
        ],
      }
      setDashboards(prev => [...prev, newDashboard])
      setSelectedDashboardId(newDashboard.id)
      setShowCreateDashboard(false)
    }
  }

  const handleDeleteDashboard = (dashboardId: string) => {
    if (confirm('Are you sure you want to delete this dashboard?')) {
      setDashboards(prev => prev.filter(d => d.id !== dashboardId))
      if (selectedDashboardId === dashboardId) {
        setSelectedDashboardId(visibleDashboards[0]?.id || '')
      }
    }
  }

  const handleDuplicateDashboard = (dashboard: Dashboard) => {
    const newDashboard: Dashboard = {
      ...dashboard,
      id: `dash-${Date.now()}`,
      name: `${dashboard.name} (Copy)`,
      isDefault: false,
      isSystemDefault: false,
      createdBy: CURRENT_USER.id,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
      widgets: dashboard.widgets.map(w => ({ ...w, id: `${w.id}-copy-${Date.now()}` })),
    }
    setDashboards(prev => [...prev, newDashboard])
    setSelectedDashboardId(newDashboard.id)
  }

  const handleSelectDateRange = (range: string) => {
    if (range === 'Custom Range') {
      setShowCustomCalendar(true)
      setShowDatePicker(false)
    } else {
      setDateRange(range)
      setShowDatePicker(false)
    }
  }

  const handleApplyCustomDateRange = () => {
    if (customStartDate && customEndDate) {
      setDateRange(`${customStartDate} - ${customEndDate}`)
      setShowCustomCalendar(false)
      setShowDatePicker(false)
    }
  }

  const handleToggleWidget = (widgetId: string) => {
    setWidgets(prev => prev.map(w => 
      w.id === widgetId ? { ...w, visible: !w.visible } : w
    ))
  }

  const handleRemoveWidget = (widgetId: string) => {
    setWidgets(prev => prev.filter(w => w.id !== widgetId))
  }

  const handleAddWidget = (widgetTemplate: any) => {
    const newWidget: Widget = {
      id: `widget-${Date.now()}`,
      type: widgetTemplate.type,
      title: widgetTemplate.title,
      position: widgets.length + 1,
      visible: true,
      size: widgetTemplate.type === 'stat' ? 'small' : 'medium',
      chartType: widgetTemplate.chartType,
      data: widgetTemplate.type === 'stat' ? { value: '0', change: '0%', icon: widgetTemplate.icon, changeType: 'neutral' } : []
    }
    setWidgets(prev => [...prev, newWidget])
    setShowAddWidget(false)
  }

  // Drag and drop handlers
  const handleDragStart = (e: React.DragEvent, widgetId: string) => {
    if (!editMode) {
      e.preventDefault()
      return
    }
    
    setDraggedWidget(widgetId)
    e.dataTransfer.effectAllowed = 'move'
    e.dataTransfer.setData('text/plain', widgetId)
    
    setTimeout(() => {
      const target = e.currentTarget as HTMLElement
      if (target) {
        target.style.opacity = '0.5'
      }
    }, 0)
  }

  const handleDragEnd = (e: React.DragEvent) => {
    const target = e.currentTarget as HTMLElement
    if (target) {
      target.style.opacity = '1'
    }
    setDraggedWidget(null)
  }

  const handleDragOver = (e: React.DragEvent, widgetId: string) => {
    if (!editMode || !draggedWidget) return
    
    e.preventDefault()
    e.stopPropagation()
    e.dataTransfer.dropEffect = 'move'
  }

  const handleDragEnter = (e: React.DragEvent, widgetId: string) => {
    if (!editMode || !draggedWidget || draggedWidget === widgetId) return
    
    e.preventDefault()
    const target = e.currentTarget as HTMLElement
    if (target) {
      target.style.backgroundColor = 'rgba(16, 185, 129, 0.1)'
      target.style.borderColor = 'rgb(16, 185, 129)'
    }
  }

  const handleDragLeave = (e: React.DragEvent, widgetId: string) => {
    if (!editMode) return
    
    const target = e.currentTarget as HTMLElement
    if (target) {
      target.style.backgroundColor = ''
      target.style.borderColor = ''
    }
  }

  const handleDrop = (e: React.DragEvent, targetWidgetId: string) => {
    e.preventDefault()
    e.stopPropagation()
    
    if (!editMode || !draggedWidget || draggedWidget === targetWidgetId) {
      setDraggedWidget(null)
      return
    }

    const visibleWidgets = widgets.filter(w => w.visible)
    const draggedWidget_obj = visibleWidgets.find(w => w.id === draggedWidget)
    const targetWidget_obj = visibleWidgets.find(w => w.id === targetWidgetId)

    if (!draggedWidget_obj || !targetWidget_obj) {
      setDraggedWidget(null)
      return
    }

    const draggedIndex = visibleWidgets.indexOf(draggedWidget_obj)
    const targetIndex = visibleWidgets.indexOf(targetWidget_obj)

    const reorderedVisible = [...visibleWidgets]
    const [removed] = reorderedVisible.splice(draggedIndex, 1)
    reorderedVisible.splice(targetIndex, 0, removed)

    const updatedVisible = reorderedVisible.map((w, index) => ({
      ...w,
      position: index + 1
    }))

    const hiddenWidgets = widgets.filter(w => !w.visible)
    const allWidgets = [...updatedVisible, ...hiddenWidgets]

    setWidgets(allWidgets)
    setDraggedWidget(null)

    const target = e.currentTarget as HTMLElement
    if (target) {
      target.style.backgroundColor = ''
      target.style.borderColor = ''
    }
  }

  const visibleWidgets = widgets.filter(w => w.visible).sort((a, b) => a.position - b.position)

  // Get visibility badge
  const getVisibilityBadge = (access: DashboardAccess) => {
    switch (access.visibility) {
      case 'public':
        return { label: 'Public', color: 'bg-green-100 text-green-700', icon: 'üåê' }
      case 'role':
        return { label: 'Role Restricted', color: 'bg-blue-100 text-blue-700', icon: 'üë•' }
      case 'user':
        return { label: 'Private', color: 'bg-purple-100 text-purple-700', icon: 'üîí' }
      default:
        return { label: 'Unknown', color: 'bg-gray-100 text-gray-700', icon: '‚ùì' }
    }
  }

  return (
    <div className="space-y-6">
      {/* Dashboard Tabs Navigation */}
      <div className="border-b border-gray-200">
        <div className="flex items-center justify-between">
          <nav className="-mb-px flex space-x-1 overflow-x-auto pb-px" aria-label="Dashboard tabs">
            {visibleDashboards.map((dashboard) => {
              const isActive = selectedDashboardId === dashboard.id
              const isDefault = dashboard.id === userDefaultDashboardId
              const badge = getVisibilityBadge(dashboard.access)
              
              return (
                <button
                  key={dashboard.id}
                  onClick={() => handleSelectDashboard(dashboard.id)}
                  className={`group relative whitespace-nowrap border-b-2 py-3 px-4 text-sm font-medium transition-colors flex items-center gap-2 ${
                    isActive
                      ? 'border-green-500 text-green-600'
                      : 'border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700'
                  }`}
                >
                  <span className="text-lg">{dashboard.icon || 'üìä'}</span>
                  <span>{dashboard.name}</span>
                  {isDefault && (
                    <span className="ml-1 text-xs bg-yellow-100 text-yellow-700 px-1.5 py-0.5 rounded-full">
                      Default
                    </span>
                  )}
                  {/* Hover tooltip with visibility info */}
                  <div className="absolute bottom-full left-1/2 -translate-x-1/2 mb-2 hidden group-hover:block z-50">
                    <div className="bg-gray-900 text-white text-xs rounded-lg py-2 px-3 whitespace-nowrap">
                      <div className="flex items-center gap-1 mb-1">
                        <span>{badge.icon}</span>
                        <span>{badge.label}</span>
                      </div>
                      {dashboard.description && (
                        <p className="text-gray-300 text-xs max-w-[200px]">{dashboard.description}</p>
                      )}
                    </div>
                    <div className="absolute top-full left-1/2 -translate-x-1/2 border-4 border-transparent border-t-gray-900"></div>
                  </div>
                </button>
              )
            })}
            
            {/* Add Dashboard Button */}
            <button
              onClick={handleCreateDashboard}
              className="whitespace-nowrap border-b-2 border-transparent py-3 px-4 text-sm font-medium text-gray-400 hover:text-green-600 hover:border-green-300 transition-colors flex items-center gap-2"
            >
              <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
              </svg>
              <span>New Dashboard</span>
            </button>
          </nav>
        </div>
      </div>

      {/* Page Header with Dashboard Controls */}
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
        {/* Left: Dashboard Info */}
        <div className="flex items-center gap-4">
          <div>
            <div className="flex items-center gap-2">
              <span className="text-3xl">{currentDashboard?.icon || 'üìä'}</span>
              <h1 className="text-2xl font-bold text-gray-900">{currentDashboard?.name || 'Dashboard'}</h1>
              {currentDashboard?.id === userDefaultDashboardId && (
                <span className="text-xs bg-yellow-100 text-yellow-700 px-2 py-1 rounded-full font-medium">
                  Your Default
                </span>
              )}
            </div>
            <p className="mt-1 text-sm text-gray-600">
              {editMode ? 'Drag widgets to reorder, toggle visibility, or add new widgets' : currentDashboard?.description || 'Overview of all metrics and performance'}
            </p>
          </div>
        </div>

        {/* Right: Action Buttons */}
        <div className="flex items-center gap-3 flex-wrap">
          {/* Dashboard Actions Dropdown */}
          <div className="relative group">
            <button
              className="flex items-center gap-2 px-3 py-2 bg-white border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors text-sm"
            >
              <svg className="w-4 h-4 text-gray-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z" />
              </svg>
              <span>Actions</span>
            </button>
            
            <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border border-gray-200 py-1 z-50 hidden group-hover:block">
              {currentDashboard?.id !== userDefaultDashboardId && (
                <button
                  onClick={() => handleSetAsDefault(currentDashboard?.id || '')}
                  className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100 flex items-center gap-2"
                >
                  <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11.049 2.927c.3-.921 1.603-.921 1.902 0l1.519 4.674a1 1 0 00.95.69h4.915c.969 0 1.371 1.24.588 1.81l-3.976 2.888a1 1 0 00-.363 1.118l1.518 4.674c.3.922-.755 1.688-1.538 1.118l-3.976-2.888a1 1 0 00-1.176 0l-3.976 2.888c-.783.57-1.838-.197-1.538-1.118l1.518-4.674a1 1 0 00-.363-1.118l-3.976-2.888c-.784-.57-.38-1.81.588-1.81h4.914a1 1 0 00.951-.69l1.519-4.674z" />
                  </svg>
                  Set as Default
                </button>
              )}
              <button
                onClick={handleOpenDashboardSettings}
                className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100 flex items-center gap-2"
              >
                <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z" />
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                </svg>
                Dashboard Settings
              </button>
              <button
                onClick={() => handleDuplicateDashboard(currentDashboard!)}
                className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100 flex items-center gap-2"
              >
                <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                </svg>
                Duplicate Dashboard
              </button>
              {currentDashboard && !currentDashboard.isSystemDefault && (
                <button
                  onClick={() => handleDeleteDashboard(currentDashboard.id)}
                  className="w-full px-4 py-2 text-left text-sm text-red-600 hover:bg-red-50 flex items-center gap-2"
                >
                  <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                  </svg>
                  Delete Dashboard
                </button>
              )}
            </div>
          </div>

          {/* Date Range Selector */}
          <div className="relative">
            <button
              onClick={() => setShowDatePicker(!showDatePicker)}
              className="flex items-center gap-2 px-4 py-2 bg-white border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
            >
              <svg className="w-5 h-5 text-gray-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
              </svg>
              <span className="text-sm font-medium text-gray-700">{dateRange}</span>
              <svg className="w-4 h-4 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </button>

            {showDatePicker && (
              <div className="absolute right-0 mt-2 w-56 bg-white rounded-lg shadow-lg border border-gray-200 py-2 z-50">
                <div className="px-4 py-2 border-b border-gray-200">
                  <p className="text-xs font-semibold text-gray-500 uppercase">Select Date Range</p>
                </div>
                {dateRanges.map((range) => (
                  <button
                    key={range}
                    onClick={() => handleSelectDateRange(range)}
                    className={`w-full px-4 py-2 text-left text-sm hover:bg-gray-100 ${
                      dateRange === range ? 'bg-green-50 text-green-700' : 'text-gray-700'
                    }`}
                  >
                    {range}
                  </button>
                ))}
              </div>
            )}
          </div>

          {/* Edit Dashboard Button */}
          <button
            onClick={handleEditDashboard}
            className={`flex items-center gap-2 px-4 py-2 rounded-lg transition-colors ${
              editMode 
                ? 'bg-green-600 text-white hover:bg-green-700' 
                : 'bg-white border border-gray-300 text-gray-700 hover:bg-gray-50'
            }`}
          >
            <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              {editMode ? (
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
              ) : (
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
              )}
            </svg>
            <span className="text-sm font-medium">{editMode ? 'Done Editing' : 'Edit Widgets'}</span>
          </button>
        </div>
      </div>

      {/* Widget Manager Panel (shown in edit mode) */}
      {showWidgetManager && (
        <div className="bg-green-50 border border-green-200 rounded-lg p-4">
          <div className="flex items-start justify-between mb-4">
            <div>
              <h3 className="text-lg font-semibold text-gray-900 flex items-center gap-2">
                <span>üéõÔ∏è</span>
                Widget Manager
              </h3>
              <p className="text-sm text-gray-600 mt-1">
                Toggle visibility, reorder by dragging, or add new widgets
              </p>
            </div>
            <button
              onClick={() => setShowAddWidget(true)}
              className="px-4 py-2 bg-green-600 text-white text-sm rounded-lg hover:bg-green-700 transition-colors flex items-center gap-2"
            >
              <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
              </svg>
              Add Widget
            </button>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-3">
            {widgets.map((widget) => (
              <div
                key={widget.id}
                className={`bg-white rounded-lg border-2 p-3 transition-all ${
                  widget.visible ? 'border-green-300' : 'border-gray-200 opacity-60'
                }`}
              >
                <div className="flex items-start justify-between">
                  <div className="flex items-center gap-2 flex-1">
                    <button
                      className="text-gray-400 hover:text-gray-600 cursor-move"
                      title="Drag to reorder"
                    >
                      <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 8h16M4 16h16" />
                      </svg>
                    </button>
                    <div className="flex-1">
                      <p className="text-sm font-medium text-gray-900">{widget.title}</p>
                      <p className="text-xs text-gray-500 capitalize">{widget.type} ‚Ä¢ {widget.size}</p>
                    </div>
                  </div>
                  <div className="flex items-center gap-2">
                    <button
                      onClick={() => handleToggleWidget(widget.id)}
                      className={`p-1 rounded transition-colors ${
                        widget.visible ? 'text-green-600 hover:bg-green-50' : 'text-gray-400 hover:bg-gray-100'
                      }`}
                      title={widget.visible ? 'Hide widget' : 'Show widget'}
                    >
                      <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        {widget.visible ? (
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                        ) : (
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.88 9.88l-3.29-3.29m7.532 7.532l3.29 3.29M3 3l3.59 3.59m0 0A9.953 9.953 0 0112 5c4.478 0 8.268 2.943 9.543 7a10.025 10.025 0 01-4.132 5.411m0 0L21 21" />
                        )}
                      </svg>
                    </button>
                    <button
                      onClick={() => handleRemoveWidget(widget.id)}
                      className="p-1 text-red-600 hover:bg-red-50 rounded transition-colors"
                      title="Remove widget"
                    >
                      <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                      </svg>
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Dashboard Widgets Grid */}
      <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
        {visibleWidgets.map((widget) => {
          const gridClass = 
            widget.size === 'full' ? 'col-span-1 sm:col-span-2 lg:col-span-4' :
            widget.size === 'large' ? 'col-span-1 sm:col-span-2 lg:col-span-3' :
            widget.size === 'medium' ? 'col-span-1 sm:col-span-2 lg:col-span-2' :
            'col-span-1'

          return (
            <div
              key={widget.id}
              draggable={editMode}
              onDragStart={(e) => handleDragStart(e, widget.id)}
              onDragEnd={handleDragEnd}
              onDragOver={(e) => handleDragOver(e, widget.id)}
              onDragEnter={(e) => handleDragEnter(e, widget.id)}
              onDragLeave={(e) => handleDragLeave(e, widget.id)}
              onDrop={(e) => handleDrop(e, widget.id)}
              className={`bg-white shadow-sm rounded-lg border-2 hover:shadow-md transition-all ${gridClass} ${
                editMode ? 'cursor-move ring-2 ring-green-300 select-none' : 'border-gray-200'
              } ${draggedWidget === widget.id ? 'opacity-50' : ''}`}
              style={{ 
                transition: 'all 0.2s ease-in-out',
                userSelect: editMode ? 'none' : 'auto'
              }}
            >
              {/* Stat Widget */}
              {widget.type === 'stat' && (
                <div className="p-5">
                  <div className="flex items-center">
                    <div className="flex-shrink-0">
                      <span className="text-4xl">{widget.data.icon}</span>
                    </div>
                    <div className="ml-5 w-0 flex-1">
                      <dl>
                        <dt className="text-sm font-medium text-gray-500 truncate">
                          {widget.title}
                        </dt>
                        <dd className="flex items-baseline">
                          <div className="text-2xl font-semibold text-gray-900">
                            {widget.data.value}
                          </div>
                          <div
                            className={`ml-2 flex items-baseline text-sm font-semibold ${
                              widget.data.changeType === 'positive'
                                ? 'text-green-600'
                                : widget.data.changeType === 'negative'
                                ? 'text-red-600'
                                : 'text-gray-500'
                            }`}
                          >
                            {widget.data.change}
                          </div>
                        </dd>
                      </dl>
                    </div>
                  </div>
                </div>
              )}

              {/* Chart Widget */}
              {widget.type === 'chart' && (
                <>
                  <div className="px-6 py-5 border-b border-gray-200">
                    <h3 className="text-lg font-medium text-gray-900">{widget.title}</h3>
                  </div>
                  <div className="p-6">
                    <div className={widget.size === 'full' ? 'h-80' : 'h-64'}>
                      <canvas id={`chart-${widget.id}`}></canvas>
                    </div>
                  </div>
                </>
              )}

              {/* List Widget */}
              {widget.type === 'list' && (
                <>
                  <div className="px-6 py-5 border-b border-gray-200">
                    <h3 className="text-lg font-medium text-gray-900">{widget.title}</h3>
                  </div>
                  <div className="divide-y divide-gray-200">
                    {widget.data.slice(0, 4).map((item: any, index: number) => (
                      <div key={index} className="px-6 py-4 hover:bg-gray-50">
                        <div className="flex items-center justify-between">
                          <div className="flex-1 min-w-0">
                            <p className="text-sm font-medium text-gray-900 truncate">
                              {item.name || item.user}
                            </p>
                            <p className="text-sm text-gray-500">
                              {item.leads ? `${item.leads} leads ‚Ä¢ ${item.conversion} conversion` : item.action}
                            </p>
                          </div>
                          {item.trend && (
                            <div className="ml-4 flex-shrink-0">
                              <span
                                className={`inline-flex items-center text-sm font-medium ${
                                  item.trend === 'up' ? 'text-green-600' : 'text-red-600'
                                }`}
                              >
                                {item.trend === 'up' ? '‚Üë' : '‚Üì'}
                              </span>
                            </div>
                          )}
                          {item.time && (
                            <div className="ml-4 flex-shrink-0">
                              <p className="text-xs text-gray-400">{item.time}</p>
                            </div>
                          )}
                        </div>
                      </div>
                    ))}
                  </div>
                </>
              )}
            </div>
          )
        })}
      </div>

      {/* Custom Calendar Modal */}
      {showCustomCalendar && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg shadow-xl max-w-md w-full">
            <div className="px-6 py-4 border-b border-gray-200 flex items-center justify-between">
              <h3 className="text-lg font-semibold text-gray-900">Select Custom Date Range</h3>
              <button
                onClick={() => setShowCustomCalendar(false)}
                className="text-gray-400 hover:text-gray-600"
              >
                <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Start Date
                </label>
                <input
                  type="date"
                  value={customStartDate}
                  onChange={(e) => setCustomStartDate(e.target.value)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  End Date
                </label>
                <input
                  type="date"
                  value={customEndDate}
                  onChange={(e) => setCustomEndDate(e.target.value)}
                  min={customStartDate}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>

              <div className="pt-4 border-t border-gray-200">
                <p className="text-xs font-semibold text-gray-500 uppercase mb-2">Quick Select</p>
                <div className="grid grid-cols-2 gap-2">
                  <button
                    onClick={() => {
                      const today = new Date()
                      const lastWeek = new Date(today)
                      lastWeek.setDate(today.getDate() - 7)
                      setCustomStartDate(lastWeek.toISOString().split('T')[0])
                      setCustomEndDate(today.toISOString().split('T')[0])
                    }}
                    className="px-3 py-2 text-xs bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors"
                  >
                    Last 7 Days
                  </button>
                  <button
                    onClick={() => {
                      const today = new Date()
                      const lastMonth = new Date(today)
                      lastMonth.setDate(today.getDate() - 30)
                      setCustomStartDate(lastMonth.toISOString().split('T')[0])
                      setCustomEndDate(today.toISOString().split('T')[0])
                    }}
                    className="px-3 py-2 text-xs bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors"
                  >
                    Last 30 Days
                  </button>
                  <button
                    onClick={() => {
                      const today = new Date()
                      const lastQuarter = new Date(today)
                      lastQuarter.setDate(today.getDate() - 90)
                      setCustomStartDate(lastQuarter.toISOString().split('T')[0])
                      setCustomEndDate(today.toISOString().split('T')[0])
                    }}
                    className="px-3 py-2 text-xs bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors"
                  >
                    Last 90 Days
                  </button>
                  <button
                    onClick={() => {
                      const today = new Date()
                      const firstDay = new Date(today.getFullYear(), today.getMonth(), 1)
                      setCustomStartDate(firstDay.toISOString().split('T')[0])
                      setCustomEndDate(today.toISOString().split('T')[0])
                    }}
                    className="px-3 py-2 text-xs bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors"
                  >
                    This Month
                  </button>
                </div>
              </div>
            </div>

            <div className="px-6 py-4 bg-gray-50 rounded-b-lg flex items-center justify-end gap-3">
              <button
                onClick={() => setShowCustomCalendar(false)}
                className="px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={handleApplyCustomDateRange}
                disabled={!customStartDate || !customEndDate}
                className="px-4 py-2 text-sm font-medium bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Apply Date Range
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Add Widget Modal */}
      {showAddWidget && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full max-h-[80vh] overflow-y-auto">
            <div className="px-6 py-4 border-b border-gray-200 flex items-center justify-between sticky top-0 bg-white">
              <h3 className="text-lg font-semibold text-gray-900">Add New Widget</h3>
              <button
                onClick={() => setShowAddWidget(false)}
                className="text-gray-400 hover:text-gray-600"
              >
                <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-6">
              <p className="text-sm text-gray-600 mb-4">
                Select a widget to add to your dashboard
              </p>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {availableWidgets.map((widgetTemplate) => (
                  <button
                    key={widgetTemplate.id}
                    onClick={() => handleAddWidget(widgetTemplate)}
                    className="p-4 border-2 border-gray-200 rounded-lg hover:border-green-500 hover:bg-green-50 transition-all text-left group"
                  >
                    <div className="flex items-start gap-3">
                      <span className="text-3xl">{widgetTemplate.icon}</span>
                      <div className="flex-1">
                        <h4 className="font-medium text-gray-900 group-hover:text-green-700">
                          {widgetTemplate.title}
                        </h4>
                        <p className="text-xs text-gray-500 capitalize mt-1">
                          {widgetTemplate.type} widget
                          {widgetTemplate.chartType && ` ‚Ä¢ ${widgetTemplate.chartType} chart`}
                        </p>
                      </div>
                      <svg className="w-5 h-5 text-gray-400 group-hover:text-green-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                      </svg>
                    </div>
                  </button>
                ))}
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Create/Edit Dashboard Modal */}
      {(showCreateDashboard || showDashboardSettings) && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg shadow-xl max-w-lg w-full max-h-[90vh] overflow-y-auto">
            <div className="px-6 py-4 border-b border-gray-200 flex items-center justify-between sticky top-0 bg-white">
              <h3 className="text-lg font-semibold text-gray-900">
                {editingDashboard ? 'Dashboard Settings' : 'Create New Dashboard'}
              </h3>
              <button
                onClick={() => {
                  setShowCreateDashboard(false)
                  setShowDashboardSettings(false)
                  setEditingDashboard(null)
                }}
                className="text-gray-400 hover:text-gray-600"
              >
                <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-6 space-y-6">
              {/* Dashboard Name */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Dashboard Name <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={dashboardForm.name}
                  onChange={(e) => setDashboardForm(prev => ({ ...prev, name: e.target.value }))}
                  placeholder="e.g., Sales Performance"
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>

              {/* Description */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Description
                </label>
                <textarea
                  value={dashboardForm.description}
                  onChange={(e) => setDashboardForm(prev => ({ ...prev, description: e.target.value }))}
                  placeholder="Brief description of this dashboard..."
                  rows={2}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>

              {/* Icon Selection */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Icon
                </label>
                <div className="flex flex-wrap gap-2">
                  {dashboardIcons.map((icon) => (
                    <button
                      key={icon}
                      onClick={() => setDashboardForm(prev => ({ ...prev, icon }))}
                      className={`w-10 h-10 text-xl flex items-center justify-center rounded-lg border-2 transition-colors ${
                        dashboardForm.icon === icon
                          ? 'border-green-500 bg-green-50'
                          : 'border-gray-200 hover:border-gray-300'
                      }`}
                    >
                      {icon}
                    </button>
                  ))}
                </div>
              </div>

              {/* Visibility Settings */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-3">
                  Access Settings
                </label>
                <div className="space-y-3">
                  {/* Public */}
                  <label className={`flex items-start gap-3 p-3 rounded-lg border-2 cursor-pointer transition-colors ${
                    dashboardForm.visibility === 'public' ? 'border-green-500 bg-green-50' : 'border-gray-200 hover:border-gray-300'
                  }`}>
                    <input
                      type="radio"
                      name="visibility"
                      value="public"
                      checked={dashboardForm.visibility === 'public'}
                      onChange={() => setDashboardForm(prev => ({ ...prev, visibility: 'public', allowedRoles: [], allowedUserIds: [] }))}
                      className="mt-1 text-green-600 focus:ring-green-500"
                    />
                    <div>
                      <div className="flex items-center gap-2">
                        <span>üåê</span>
                        <span className="font-medium text-gray-900">Public</span>
                      </div>
                      <p className="text-sm text-gray-500 mt-1">Available to all users in the organization</p>
                    </div>
                  </label>

                  {/* Role Restricted */}
                  <label className={`flex items-start gap-3 p-3 rounded-lg border-2 cursor-pointer transition-colors ${
                    dashboardForm.visibility === 'role' ? 'border-green-500 bg-green-50' : 'border-gray-200 hover:border-gray-300'
                  }`}>
                    <input
                      type="radio"
                      name="visibility"
                      value="role"
                      checked={dashboardForm.visibility === 'role'}
                      onChange={() => setDashboardForm(prev => ({ ...prev, visibility: 'role', allowedUserIds: [] }))}
                      className="mt-1 text-green-600 focus:ring-green-500"
                    />
                    <div className="flex-1">
                      <div className="flex items-center gap-2">
                        <span>üë•</span>
                        <span className="font-medium text-gray-900">Role Restricted</span>
                      </div>
                      <p className="text-sm text-gray-500 mt-1">Available only to users with specific roles</p>
                      
                      {dashboardForm.visibility === 'role' && (
                        <div className="mt-3 space-y-2">
                          <p className="text-xs font-medium text-gray-600">Select allowed roles:</p>
                          <div className="flex flex-wrap gap-2">
                            {AVAILABLE_ROLES.map((role) => (
                              <button
                                key={role.id}
                                type="button"
                                onClick={() => {
                                  setDashboardForm(prev => ({
                                    ...prev,
                                    allowedRoles: prev.allowedRoles.includes(role.id)
                                      ? prev.allowedRoles.filter(r => r !== role.id)
                                      : [...prev.allowedRoles, role.id]
                                  }))
                                }}
                                className={`px-3 py-1.5 text-xs rounded-full border transition-colors ${
                                  dashboardForm.allowedRoles.includes(role.id)
                                    ? 'bg-blue-100 border-blue-300 text-blue-700'
                                    : 'bg-gray-100 border-gray-200 text-gray-600 hover:bg-gray-200'
                                }`}
                              >
                                {role.name}
                              </button>
                            ))}
                          </div>
                        </div>
                      )}
                    </div>
                  </label>

                  {/* User Specific */}
                  <label className={`flex items-start gap-3 p-3 rounded-lg border-2 cursor-pointer transition-colors ${
                    dashboardForm.visibility === 'user' ? 'border-green-500 bg-green-50' : 'border-gray-200 hover:border-gray-300'
                  }`}>
                    <input
                      type="radio"
                      name="visibility"
                      value="user"
                      checked={dashboardForm.visibility === 'user'}
                      onChange={() => setDashboardForm(prev => ({ ...prev, visibility: 'user', allowedRoles: [] }))}
                      className="mt-1 text-green-600 focus:ring-green-500"
                    />
                    <div className="flex-1">
                      <div className="flex items-center gap-2">
                        <span>üîí</span>
                        <span className="font-medium text-gray-900">Private / User Specific</span>
                      </div>
                      <p className="text-sm text-gray-500 mt-1">Available only to specific selected users</p>
                      
                      {dashboardForm.visibility === 'user' && (
                        <div className="mt-3 space-y-2">
                          <p className="text-xs font-medium text-gray-600">Select allowed users:</p>
                          <div className="space-y-2 max-h-40 overflow-y-auto">
                            {AVAILABLE_USERS.map((user) => (
                              <label
                                key={user.id}
                                className={`flex items-center gap-3 p-2 rounded-lg cursor-pointer transition-colors ${
                                  dashboardForm.allowedUserIds.includes(user.id)
                                    ? 'bg-purple-50 border border-purple-200'
                                    : 'hover:bg-gray-50'
                                }`}
                              >
                                <input
                                  type="checkbox"
                                  checked={dashboardForm.allowedUserIds.includes(user.id)}
                                  onChange={() => {
                                    setDashboardForm(prev => ({
                                      ...prev,
                                      allowedUserIds: prev.allowedUserIds.includes(user.id)
                                        ? prev.allowedUserIds.filter(u => u !== user.id)
                                        : [...prev.allowedUserIds, user.id]
                                    }))
                                  }}
                                  className="text-purple-600 focus:ring-purple-500 rounded"
                                />
                                <div>
                                  <p className="text-sm font-medium text-gray-900">{user.name}</p>
                                  <p className="text-xs text-gray-500">{user.email}</p>
                                </div>
                              </label>
                            ))}
                          </div>
                        </div>
                      )}
                    </div>
                  </label>
                </div>
              </div>
            </div>

            <div className="px-6 py-4 bg-gray-50 rounded-b-lg flex items-center justify-end gap-3 sticky bottom-0">
              <button
                onClick={() => {
                  setShowCreateDashboard(false)
                  setShowDashboardSettings(false)
                  setEditingDashboard(null)
                }}
                className="px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={handleSaveDashboard}
                disabled={!dashboardForm.name.trim() || (dashboardForm.visibility === 'role' && dashboardForm.allowedRoles.length === 0) || (dashboardForm.visibility === 'user' && dashboardForm.allowedUserIds.length === 0)}
                className="px-4 py-2 text-sm font-medium bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {editingDashboard ? 'Save Changes' : 'Create Dashboard'}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
